@import url("https://fonts.googleapis.com/css2?family=Mitr&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Mitr", sans-serif;
  scroll-behavior: smooth;
}

section {
  background: #d3d3d3;
  width: 80vw;
  height: 80vh;
  border-radius: 10px;
  border: 5px solid rgba(46, 46, 46, 0.952);
  overflow: auto;
}

h2 {
  padding: 15px 0px 15px 0px;
  display: flex;
  justify-content: center;
  color: rgb(31, 31, 31);
}

hr {
  border-radius: 10px;
  height: 5px;
  background: #333;
}

table {
  margin-top: 15px;
  height: 60vh;
  border-collapse: collapse;
  text-align: center;
  font-size: 1.1rem;
}

table b {
  color: #ff0000;
}

.App {
  font-size: 1.2rem;
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background-size: 2000% 400%;
  animation: gradient 30s ease infinite;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  flex-direction: columns;
  flex-wrap: wrap;
}

#footer {
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0px 30px 0px 30px;
  color: #fff;
  font-size: 2rem;
  background: #f86969;
  border-radius: 10px;
  height: 55px;
}

#footer a {
  font-size: 1rem;
  padding: 0px 40px 0px 5px;
  text-decoration: none;
  color: #fff;
  transition: .5s;
} 

#footer a:hover {
  color: rgb(52, 253, 96);
  transition: .5s;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 80% 50%;
  }
}

@media screen and (max-width: 600px) {
  h2 {
    font-size: 1.2rem;
  }

  section {
    width: 95vw;
    height: 95vh;
    border-radius: 10px;
    border: 5px solid rgba(46, 46, 46, 0.952);
    overflow: scroll;
  }

  table {
    font-size: 1rem;
    height: max-content;
  }

  #footer {
  width: 95%;
  display: flex;
  padding: 0px 0px 0px 10px;
  font-size: 1rem;
  background: #f86969;
  border-radius: 5px;
  height: 40px;
}

#footer a {
  font-size: 1rem;
  padding-left: 10px;
  text-decoration: none;
  color: #fff;
  transition: .5s;
} 
}
